<template>
  <div class="manage-examine manage-dev">
    <div class="title-line">
      <p class="title"></p>
      <p class="org">{{ BELONGTO_LIST[this.user_info.org] }}</p>
    </div>
    <el-tabs class="table-tab" v-model="status" @tab-click="handleClick">
      <el-tab-pane label="" name="all"></el-tab-pane>
      <el-tab-pane label="" name="4"></el-tab-pane>
      <el-tab-pane label="" name="5"></el-tab-pane>
      <el-tab-pane label="" name="7"></el-tab-pane>
      <el-tab-pane label="" name="6"></el-tab-pane>
    </el-tabs>
    <!-- <p class="manage-examine-remain title">{{ pagination.total }}</p> -->
    <pager
      :pageSize="6"
      :requestFunc="getList"
      :errorMsg="''"
      :extraData="status"
      ref="refresh">
      <el-table
        v-loading="loading"
        class="table-project"
        slot-scope="props" 
        :data="props.list">
        <el-table-column label="" width="160">
          <template slot-scope="scope">
            <div class="line">{{scope.row.project_name}}</div>
          </template>
        </el-table-column>
        <el-table-column label="" width="220">
          <template slot-scope="scope">
            <div class="line">{{scope.row.place}}</div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="line">{{ scope.row.status | statusToReadable }}</div>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="200">
          <template slot-scope="scope">
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="" 
              placement="top">
              <el-button 
                icon="el-icon-edit" circle 
                type="primary" 
                size="small" 
                class="button-lists-button"
                @click="Suggest(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="" 
              placement="top">
              <el-button 
                type="info" 
                class="button-lists-button"
                icon="el-icon-more" circle
                size="small"
                @click="openDialog(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="" 
              placement="top">
              <el-button 
                icon="el-icon-success" circle
                type="success"
                class="button-lists-button"
                size="small"
                v-show="canPass(scope.row)"
                @click="projectPass(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="" 
              placement="top">
              <el-button 
                icon="el-icon-error" circle
                type="danger"
                class="button-lists-button"
                size="small"
                v-show="canReject(scope.row)"
                @click="projectReject(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </pager>
    <el-dialog
      title=""
      :fullscreen="isPhone"
      v-dialogDrag
      :visible.sync="dialogVisible">
      <TeamInfoDisplay :team-info="dialogRow" :teammates-info="teammates"/>
      <el-button round type="success"
        :disabled="!canPass(dialogRow)"
        @click="projectPass(dialogRow)"></el-button>
      <el-button round type="danger"
        :disabled="!canReject(dialogRow)"
        @click="projectReject(dialogRow)"></el-button>
      <el-button round type="success" 
        size="small" 
        @click="Suggest(dialogRow)"></el-button>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="suggestDialogVisible"
      :fullscreen="isPhone"
      v-dialogDrag>
      <el-input
        type="textarea"
        class="suggest-content"
        :rows="4"
        :cols="10"
        v-model="suggestion"
        placeholder="">
      </el-input>
      <el-button round type="success"
        @click="submitSuggestion()"></el-button>
      <el-button round type="info" @click="suggestDialogVisible = false"></el-button>
    </el-dialog>
  </div>
</template>

<script>
import Pager from '@/components/ArtPager'
import TeamInfoDisplay from '@/components/TeamInfoDisplay'
import statusMixin from '@/mixins/status'
import { mapGetters, mapMutations } from 'vuex';
import { showAsynConfirm } from '@/mixins/messageBox'
import { getTeammateInfo, getAdminTeams, teamIsPass, suggestion} from '@/api/team'

export default {
  name: 'ManageCheck',
  mixins:[statusMixin],
  components: {
    Pager,
    TeamInfoDisplay
  },
  data() {
    return{
      suggestion: '',
      suggestDialogVisible: false,
      loading: false,
      status: 'all',
      projects: [],
      teammates:[],
      dialogVisible: false,
      dialogRow: {},
      BELONGTO_LIST: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
      ],
    }
  },
  computed: {
    ...mapGetters({
      user_info: 'user_info',
      isPhone: 'isPhone',
      currentTeamId: 'currentTeamId',
    })
  },
  mounted() {
    document.getElementById("manage-examine").scrollIntoView();
  },
  methods: {
    ...mapMutations ({
      saveCurrentTeamId: 'saveCurrentTeamId',
    }),
    //pager
    getList: getAdminTeams,
    // 
    async _teamIsPass(team_id, data) {
      try {
        const return_data = await teamIsPass(team_id, data)
        this.changePages(1)
      } catch(message) {
        this.$message({
          type: 'error',
          message:  '' + message
        })
      }
    },
    _check(row, msg, boolean) {   
      showAsynConfirm(msg, '', (done) => {
        this._teamIsPass(row.team_id, boolean);
        done()
        this.changePages(1)
      });
    },
    // 
    projectPass(row) {   
      this._check(row, '', 1);
    },
    // 
    projectReject(row) {    
      this._check(row, '', 0);
    },
    // 
    canPass(row) {
      return row.status == 5 || row.status == 6;
    },
    // 
    canReject(row) {
      return row.status == 5 || row.status == 7;
    },
    async getTeams() {
      this.loading = true
      try {
        const data = await getAdminTeams(this.status)
        this.projects = data.data
        this.loading = false
      } catch(message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        });
      }
    },
    async getTeammateInfo() {
      this.loading = true
      try {
        const data = await getTeammateInfo(this.currentTeamId)
        this.teammates = data.data
        this.loading = false
      } catch(message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        });
      }
    },
    // 
    changePages(i) {
      this.$refs.refresh.turn(i)
    },
    // 
    handleClick() {
      setTimeout(() => {
        this.changePages(1)
      }, 200);
    },
    // teams
    openDialog(row) {
      this.dialogRow = row;
      this.saveCurrentTeamId(row.team_id)
      setTimeout(() => {
        this.getTeammateInfo();
      }, 100);
      setTimeout(() => {
        this.dialogVisible = true;
      }, 300);
    },
    // dialog
    Suggest(row) {
      this.suggestDialogVisible = true
      this.timelyRow = row
      this.saveCurrentTeamId(row.team_id)
    },
    submitSuggestion() {
      showAsynConfirm('', '', (done) => {
        this._submitSuggestion(done)
      })
    },
    async _submitSuggestion(done) {
      done()
      try {
        const data = await suggestion(this.currentTeamId, this.suggestion)
        this.$message({
          type: 'success',
          message: '' 
        });
        this.suggestDialogVisible = false
        this.suggestion = ''
        this.changePages(1)
      } catch(message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        });
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.manage-examine {
  .manage-examine-remain {
    margin: 0.5em 0;
    margin-left: -1.8em;
  }
  .button-lists-button {
    margin: 0 0.5em;
  }
  .title {
    font-size: 25px;
    font-family: "Microsoft YaHei";
    color: rgb(78, 64, 53);
    font-weight: bold;
    transform: scale(0.94, 1.08);
    margin-bottom: 15px;
    margin-left: -1.0em;
    margin-top: 0.3em;
  }
  .suggest-content {
    margin-bottom: 1em;
  }
  .title-line {
    display: flex;

    .title {
      flex-grow: 1;
    }
  }
}
.el-menu {
  background-color: #fdf5ee
}

@media screen and (max-width: 1000px) {
  .manage-dev{
    .title-line {
      font-size: 16px;
      .title {
        width: 80px;
        font-size: 16px;
        margin-left: 10px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 16px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 15px;
    }
    .el-table .cell {
      font-size: 15px;
    }
    .el-table th, .el-table td {
      padding: 8px 0;
    }
  }
}

@media screen and (max-width: 700px) {
  .manage-dev{
    .title-line {
      font-size: 14px;
      .title {
        width: 80px;
        font-size: 14px;
        margin-left: 10px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 14px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 14px;
    }
    .el-table .cell {
      font-size: 14px;
    }
    .el-table th, .el-table td {
      padding: 6px 0;
    }
  }
}


@media screen and (max-width: 410px) {
  .manage-dev{
    .title-line {
      font-size: 12px;
      .title {
        width: 70px;
        font-size: 12px;
        margin-left: 6px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 12px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 12px;
    }
    .el-table .cell {
      font-size: 12px;
    }
    .el-table th, .el-table td {
      padding: 4px 0;
    }
  }
}
</style>

