<template>
  <el-container class="main-layout" :class="{'day':daytime}">
    <el-header class="main-layout-header" :class="{'preback':!online}" :height="online?'58vw':'62vw'">
      <div v-if="!online" class="premob premob0"></div>
      <div v-if="!online" class="premob premob1"></div>
      <div v-if="!online" class="prefore"></div>
      <div class="logo" :class="{'day':daytime}"></div>
      <div class="login">
        <span v-if="online" class="login-link">{{ user_name }}</span><!--v-if-->
        <span v-else class="login-link login-linkbtn" @click="handleLogin()"></span>
        <span v-if="online" class="login-link login-linkbtn" @click="handleLogout()"></span>
        <span v-if="online" class="login-link login-linkbtn" @click="SelfInfoEdit()"></span>
      </div>
      <img class="title noselect" src="@/assets/ui/title.png">
      <img v-if="!online" class="loginbtn" src="@/assets/ui/login.png" @click="handleLogin()">
    </el-header>
    <el-container v-if="online" class="main-layout-center" id="main-layout-center">
      <el-aside class="main-layout-aside" width="16vw">
        <nav-menu class="nav"></nav-menu>
      </el-aside>
      <el-main v-if="online" class="main-layout-main" id="main-layout-main">
        <router-view></router-view>
      </el-main>
    </el-container>
    <el-footer v-if="online" class="main-layout-footer" height="43vw">
    </el-footer>
  </el-container>
</template>

<script>
import { getUserInfo } from '@/api/profile'
import { getCurrentUserInfo, getRedirectUrl } from '@/api/cas'
import NavMenu from './NavMenu'
import { mapGetters, mapMutations } from 'vuex'
import axios from 'axios'

export default {
  name: 'Mainlayout',
  components: {
    NavMenu
  },
  data() {
    return {
      hours: '',
    }
  },
  computed: {
    ...mapGetters({
      cas_user: 'cas_user',   
      online: 'online',
    }),
    user_name() {
      return this.cas_user ? this.cas_user.user.userinfo.username : ''
    },
    daytime() {
      if(this.hours <= 19 && this.hours >=7) {
        return true;
      } else {
        return false
      }
    }
  },
  async mounted() {
    // /login/callback
    if (!this.online) {           
      try {
        const data = await getCurrentUserInfo()
        this.setCasUser(data)
        if (data.has_login) {
          this.saveOnline(true)
        }
      } catch(e) {
        console.log(e)
      }
    }
    if (this.online) {  
      // /login/callback         
      try {
        const data_user_info = await getUserInfo()
        this.setUserInfo(data_user_info)
      } catch(e) {
        this.$message({
          type: 'error',
          message: '' + e
        })
      }
    }
  },
  created() {
    this.getnow();
  },
  methods: {
    ...mapMutations({
      saveOnline: 'saveOnline',
      setCasUser: 'setCasUser',
      setUserInfo: 'setUserInfo',
    }),
    getnow() {
      var data =new Date();
      this.hours = data.getHours(); 
    },
    async handleLogin() {
      try {
        const data = await getRedirectUrl()
        location.href = data
        // caslogin
        // localhostcallback_URL="index/index/index"(localhost)
        // callback_URL"base_URL" + "/login/callback",LoginCallback.vue
        // guid
        // ps:
      } catch (message) {
        this.$message({
          type: 'error',
          message: '' + message
        })
      }
    }, 
    handleLogout() {
      axios.delete('/api/cas')
      .then(r => {
        document.write(r.data)
        this.clearStorage();
      })
      .catch(e => {
        this.$message({
          type:'error',
          message:''+e
        })
      })
    },
    SelfInfoEdit: function() {
      document.getElementById("main-layout-center").scrollIntoView({block:"end",behavior:"smooth"});
      // main-layout-center
      this.$router.push('/practice/selfinfoedit')
    },
    clearStorage() {
      const keys = [
        "team_name",
        "project_name",
        "event_background",
        "event_aim",
        "fee_transport",
        "fee_print",
        "place",
        "project_type",
        "plan_region_time",
        "delivery",
        "type",
        "resource",
        "event_content",
        "fee_other",
        "vice_leader_net_id",
        "vice_leader_id",
        "teacher_name",
        "vice_leader_name",
        "belong",
        "teacher_follow",
        "is_declared",
        "declare_type"
      ];
      for (let i in keys) {
        localStorage.setItem(keys[i], "");
      }
      this.ruleForm = [];
    },
  }
}
</script>

