<script>
import axios from "axios";
import SideLine from "@/views/Practice/SideLine";
import FitForm from "@/components/FitForm.vue";
import { mapGetters, mapMutations, mapActions } from "vuex";
import { showAsynConfirm } from "@/mixins/messageBox.js";
import { getCurrentUserInfo } from "@/api/cas";
import { setTimeout } from "timers";
import { PROVINCES } from "@/mixins/consts.js";

const mirrors = [
  {
    type: "cascader",
    label: "",
    prop: "native_area",
    options: PROVINCES,
    props: {
      value: "label",
      children: "cities"
    }
  }
];

export default {
  name: "PracticeSelfInfo",
  components: {
    FitForm,
    SideLine
  },
  data() {
    var checkNumber = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        if (Number.isInteger(Number(value)) && Number(value) > 0) {
          callback();
        } else {
          callback(new Error(""));
        }
      }, 500);
    };
    var checkNumberAndLetter = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        var reg = /^[A-Za-z0-9]{1,30}$/;
        if (!reg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      }, 500);
    };
    return {
      mirrors,
      institutes: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        },
        {
          value: "7",
          label: ""
        },
        {
          value: "8",
          label: ""
        },
        {
          value: "9",
          label: ""
        },
        {
          value: "10",
          label: ""
        },
        {
          value: "11",
          label: ""
        },
        {
          value: "12",
          label: ""
        },
        {
          value: "13",
          label: ""
        },
        {
          value: "14",
          label: ""
        },
        {
          value: "15",
          label: ""
        },
        {
          value: "16",
          label: ""
        },
        {
          value: "17",
          label: ""
        },
        {
          value: "18",
          label: ""
        },
        {
          value: "19",
          label: ""
        },
        {
          value: "20",
          label: ""
        },
        {
          value: "0",
          label: ""
        }
      ],
      ruleForm: {
        user_name: "",
        gender: "",
        nation: "",
        card_type: "",
        card_id: "",
        outlook: "",
        native_area: [],
        city: "",
        erae: "",
        minerae: "",
        grade: "",
        institute: "",
        edu_level: "",
        college: "",
        clazz: "",
        user_number: "",
        mobile: "",
        major: "",
        org: 0
      },
      rules: {
        user_name: [{ required: true, message: "", trigger: "blur" }],
        gender: [
          { required: true, message: "", trigger: "change" }
        ],
        nation: [
          { required: true, message: "", trigger: "change" }
        ],
        card_type: [
          { required: true, message: "", trigger: "change" }
        ],
        card_id: [
          { validator: checkNumberAndLetter, required: true, trigger: "blur" }
        ],
        outlook: [
          { required: true, message: "", trigger: "change" }
        ],
        native_area: [
          { required: true, message: "", trigger: "change" }
        ],
        grade: [{ required: true, message: "", trigger: "change" }],
        institute: [
          { required: true, message: "", trigger: "change" }
        ],
        edu_level: [
          { required: true, message: "", trigger: "change" }
        ],
        college: [{ required: true, message: "", trigger: "change" }],
        clazz: [{ required: true, message: "", trigger: "blur" }],
        user_number: [
          { validator: checkNumberAndLetter, required: true, trigger: "blur" }
        ],
        mobile: [{ validator: checkNumber, required: true, trigger: "blur" }],
        major: [{ required: true, message: "", trigger: "blur" }]
      },
      paramsForm: {
        nation: "",
        college: "",
        institute: "",
        gender: "",
        idtype: ""
      }
    };
  },
  computed: {
    ...mapGetters({
      cas_user: "cas_user"
    })
  },
  created() {
    this.initForm();
  },
  methods: {
    ...mapMutations({
      incrementActive: "incrementActive",
      setCasUser: "setCasUser"
    }),
    ...mapActions({}),
    initForm() {
      // 
      showAsynConfirm(
        "",
        "",
        done => {
          // vuex
          let userinfo = Object.assign({}, this.cas_user.user.userinfo);
          userinfo.edu_level = parseInt(userinfo.edu_level);
          userinfo.native_area = undefined; // native_area
          userinfo.outlook = undefined; // outlook
          if (userinfo.sex == "") {
            var gender = 1;
          } else {
            var gender = 0;
          }
          if (userinfo.idtype == "") {
            var idtype = 1;
          } else {
            var idtype = 0;
          }
          // 
          setTimeout(() => {
            //labelvalue
            this.ruleForm.user_name = userinfo.username;
            this.ruleForm.user_number = userinfo.userno;
            this.ruleForm.clazz = userinfo.class;
            this.ruleForm.gender = userinfo.sex;
            this.ruleForm.card_id = userinfo.idnumber;
            this.ruleForm.major = userinfo.speciality;
            this.ruleForm.mobile = userinfo.mobile;
            this.ruleForm.card_type = userinfo.idtype;
            this.paramsForm.gender = gender;
            this.paramsForm.idtype = idtype;
            done();
            this.$message({
              type: "success",
              message: "!"
            });
          }, 500);
        }
      );
    },
    parseForm() {
      // 
      let form = this.$refs["form"];
      const keys = [
        "major",
        "mobile",
        "user_name",
        "user_number",
        "card_id",
        "card_type",
        "grade",
        "outlook",
        "edu_level",
        "clazz",
        "gender",
        "native_area",
        "org"
      ];
      let params = {};
      let data = form.getData();
      for (let i in keys) {
        params[keys[i]] = this.ruleForm[keys[i]];
        params.native_area = data.native_area[0] + data.native_area[1];
      }
      //
      if (isNaN(this.ruleForm.gender)) {
        //paramsForm.nation
        params.gender = this.paramsForm.gender;
      } else {
        params.gender = this.ruleForm.gender;
      }
      if (isNaN(this.ruleForm.card_type)) {
        //paramsForm.nation
        params.card_type = this.paramsForm.idtype;
      } else {
        params.card_type = this.ruleForm.card_type;
      }
      if (isNaN(this.ruleForm.nation)) {
        //paramsForm.nation
        params.nation = this.paramsForm.nation;
      } else {
        params.nation = this.ruleForm.nation;
      }
      if (isNaN(this.ruleForm.college)) {
        //
        params.college = this.paramsForm.college;
      } else {
        params.college = this.ruleForm.college;
      }
      if (isNaN(this.ruleForm.institute)) {
        //
        params.institute = this.paramsForm.institute;
      } else {
        params.institute = this.ruleForm.institute;
      }
      return params;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          showAsynConfirm("", "", done => {
            this._submitForm(done);
          });
        } else {
          this.$message({
            type: "error",
            message: ""
          });
          return false;
        }
      });
    },
    _submitForm(done) {
      axios
        .post("/api/user/profile", this.parseForm())
        .then(response => {
          done();
          const data = response.data;
          if (data.success == 1) {
            //js
            this.$message({
              type: "success",
              message: ""
            });
            this.refreshCasUser();
            this.incrementActive(1);
            setTimeout(() => {
              this.$router.push("/practice");
            }, 500);
          } else if (data.success == 0) {
            this.$message({
              type: "warning",
              message: ""
            });
          } else {
            this.$message({
              type: "warning",
              message: ""
            });
          }
        })
        .catch(err => {
          done();
          this.$message({
            type: "warning",
            message: "" + err
          });
        });
    },
    async refreshCasUser() {
      const data = await getCurrentUserInfo();
      this.setCasUser(data);
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>


