<script>
import axios from "axios";
import FitForm from "@/components/FitForm.vue";
import { putProfileInfo } from "@/api/profile";
import { showAsynConfirm } from "@/mixins/messageBox.js";
import { PROVINCES } from "@/mixins/consts.js";

const mirrors = [
  {
    type: "cascader",
    label: "",
    prop: "native_area",
    options: PROVINCES,
    props: {
      value: "label",
      children: "cities"
    }
  }
];

export default {
  data() {
    var checkNumberAndLetter = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        var reg = /^[A-Za-z0-9]{1,30}$/;
        if (!reg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      }, 500);
    };
    return {
      loading: false,
      showForm: true,
      isDisplay: false,
      mirrors,
      PROVINCES,
      institutes: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        },
        {
          value: "7",
          label: ""
        },
        {
          value: "8",
          label: ""
        },
        {
          value: "9",
          label: ""
        },
        {
          value: "10",
          label: ""
        },
        {
          value: "11",
          label: ""
        },
        {
          value: "12",
          label: ""
        },
        {
          value: "13",
          label: ""
        },
        {
          value: "14",
          label: ""
        },
        {
          value: "15",
          label: ""
        },
        {
          value: "16",
          label: ""
        },
        {
          value: "17",
          label: ""
        },
        {
          value: "18",
          label: ""
        },
        {
          value: "19",
          label: ""
        },
