<template>
  <div>
    <div class="bar">
      <router-link class="back" to="/home">
        <img class="back-img" src="@/assets/ui/back.png">
        <span class="back-txt"></span>
      </router-link>
      <div class="icon">
        <img class="icon-img" src="@/assets/ui/smallnotice.png">
        <span class="icon-txt"></span>
      </div>
    </div>
    <div v-loading="loading">
      <div v-if="ndata">
        <h2>{{ ndata.title }}</h2>
        <ul class="notice-ul">
          <li>{{ ndata.author_id }}</li>
          <li>{{ ndata.type | typeToReadable }}</li>
          <li>{{ ndata.overtime }}</li>
        </ul>
        <p v-html="ndata.content"></p>
      </div>
    </div>
  </div>
</template>

<script>
import statusMixin from "@/mixins/status";
import { getNotification } from "@/api/notification";
import { getHtml } from "@/js/htmlchar.js";

export default {
  name: "Notice",
  mixins: [statusMixin],
  data() {
    return {
      ndata: null,
      loading: false
    };
  },
  computed: {
    id() {
      return this.$route.params.id;
    }
  },
  async mounted() {
    this.loading = true;
    try {
      var d = await getNotification(this.id);
      console.log(d);
      if (d.content) d.content = getHtml(d.content);
      console.log(d);
      this.ndata = d;
    } catch (e) {
      this.$message({
        type: "error",
        message: "" + e
      });
    }
    this.loading = false;
  }
};
</script>

