<template>
  <div class="panel-note panel-dev note-all">
    <div class="board">
      <p class="title"></p>
      <pager :pageSize="6" :requestFunc="getList" :errorMsg="''" ref="refresh">
        <el-table
          v-loading="loading"
          slot-scope="props"
          :data="props.list"
          class="note-board"
          style="width: 100%"
        >
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 1"></el-tag>
              <el-tag type="warning" v-if="scope.row.status == 0"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="author_id" label="" width="160"></el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="content" label=""></el-table-column>
          <el-table-column label="" width="260">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  icon="el-icon-edit"
                  circle
                  class="button-lists-button"
                  type="primary"
                  @click="handleEdit(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  icon="el-icon-message"
                  circle
                  class="button-lists-button"
                  type="success"
                  v-if="scope.row.status == 0"
                  @click="handleReply(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  type="info"
                  icon="el-icon-view"
                  circle
                  class="button-lists-button"
                  v-if="scope.row.status == 1"
                  @click="handleReply(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  class="button-lists-button"
                  @click="handleDelete(scope.row)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </pager>
    </div>
    <el-dialog title="" :visible.sync="dialogReplyVisible" :fullscreen="isPhone" v-dialogDrag>
      <div class="dialog-details">
        <p class="warn" v-if="reply.row.status == 1">
          <b></b>
        </p>
        <p>{{ reply.row.title }}</p>
        <p>{{ reply.row.author_id }}</p>
        <p>{{ reply.row.content }}</p>
        <p>{{ reply.row.create_time }}</p>
        <p class="reply" v-if="reply.row.status == 1">{{ reply.row.reply }}</p>
        <p class="reply" v-if="reply.row.status == 1">{{ reply.row.reply_time }}</p>
      </div>
      <el-input
        type="textarea"
        class="edit-content"
        :rows="4"
        v-model="reply.content"
        placeholder=""
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogReplyVisible = false"> </el-button>
        <el-button type="primary" @click="submitReply()" :loading="reply.loading"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="edit.visible"
      v-dialogDrag
      :fullscreen="isPhone"
      class="note-edit-dialog"
    >
      <div class="note-edit-content">
        <el-input class="edit-title" v-model="edit.row.title" placeholder=""></el-input>
        <el-input
          type="textarea"
          class="edit-content"
          :rows="4"
          v-model="edit.row.content"
          placeholder=""
        ></el-input>
        <el-button type="primary" @click="submitEdit()"></el-button>
        <el-button @click="edit.visible = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pager from "@/components/ArtPager";
import { mapGetters } from "vuex";
import { showAsynConfirm } from "@/mixins/messageBox";
import { getAllNote, replyNote, putNote, deleteNote } from "@/api/note";

export default {
  name: "Panelnote",
  components: {
    Pager
  },
  data() {
    return {
      loading: false,
      notes: [],
      edit: {
        row: {},
        visible: false
      },
      reply: {
        row: {},
        note_id: "",
        content: ""
      },
      dialogReplyVisible: false,
      temp: {}
    };
  },
  computed: {
    ...mapGetters({
      isPhone: "isPhone"
    })
  },
  methods: {
    //pager
    getList: getAllNote,
    //get
    async _getAllNote() {
      this.loading = true;
      try {
        const data = await getAllNote();
        this.notes = data.data;
        this.loading = false;
      } catch (message) {
        this.$message.warning(message);
      }
    },
    handleReply(row) {
      this.dialogReplyVisible = true;
      this.reply.note_id = row.note_id;
      this.reply.row = row;
    },
    submitReply() {
      if (this.reply.content == "") {
        this.$message({
          type: "warning",
          message: ""
        });
      } else {
        showAsynConfirm("", "", done => {
          this._submitReply(done);
        });
      }
    },
    async _submitReply(done) {
      try {
        console.log(this.reply.note_id, this.reply.content)
        const data = await replyNote(this.reply.note_id, this.reply.content);
        this.dialogReplyVisible = false;
        this.$message({
          type: "success",
          message: ""
        });
        this.changePages(1);
      } catch (message) {
        this.$message({
          type: "warning",
          message:  message
        });
      }
      done();
    },
    parseForm(row) {
      // 
      const keys = ["title", "content", "note_id"];
      let params = {};
      for (let i in keys) {
        params[keys[i]] = row[keys[i]];
      }
      return params;
    },
    handleEdit(data) {
      (this.edit.visible = true), (this.edit.row = this.parseForm(data));
      this.temp = this.parseForm(data);
    },
    submitEdit() {
      if (
        this.edit.row.title == this.temp.title &&
        this.edit.row.content == this.temp.content
      ) {
        this.$message({
          type: "warning",
          message: ""
        });
      } else if (this.edit.row.title == "" || this.edit.row.content == "") {
        this.$message({
          type: "warning",
          message: ""
        });
      } else {
        showAsynConfirm("", "", done => {
          this._submitEdit(done);
        });
      }
    },
    async _submitEdit(done) {
      try {
        const data = await putNote(this.edit.row.note_id, this.edit.row);
        this.$message({
          type: "success",
          message: ""
        });
      } catch (message) {
        this.$message.warning(message);
      }
      this.changePages(1);
      done();
      this.edit.visible = false;
      this.edit.row.title = "";
      this.edit.row.content = "";
    },
    // 
    handleDelete(row) {
      showAsynConfirm("", "", done => {
        this._handleDelete(row.note_id, done);
      });
    },
    //async
    async _handleDelete(id, done) {
      const delete_id = id.toString();
      try {
        const data = await deleteNote(id);
        this.$message({
          type: "success",
          message: ""
        });
      } catch (message) {
        alert(message);
      }
      this.changePages(1);
      done();
    },
    changePages(i) {
      this.$refs.refresh.turn(i);
    }
  }
};
</script>

<style lang="scss">
.edit-content {
  textarea {
    resize: none;
  }
}

@media screen and (max-width: 940px) {
  .note-all {
    .el-table .cell {
      font-size: 15px;
    }
    .el-table th, .el-table td {
      padding: 6px 0;
      width: 80px;
    }
    .el-table th > .cell {
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 540px) {
  .note-all {
    .el-table .cell {
      font-size: 12px;
    }
    .el-table th, .el-table td {
      padding: 6px 0;
      width: 80px;
    }
    .el-table th > .cell {
      font-size: 12px;
    }
  }
}
</style>

<style lang="scss" scoped>
.panel-note {
  .title {
    font-size: 25px;
    font-family: "Microsoft YaHei";
    color: rgb(78, 64, 53);
    font-weight: bold;
    transform: scale(0.94, 1.08);
    margin-bottom: 15px;
    margin-left: -1.6em;
  }
  .note-board {
    margin: 1em 0;
  }
  .editor {
    margin-top: 2em;
    .new-button {
      margin: 1em 0;
    }
    .label {
      margin: 1em 0;
    }
  }
  .button-lists-button {
    margin: 0 0.5em 0 1em;
  }
  .container {
    margin: 1em 0;
  }
  .line-new {
    padding: 1em 2em;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
}

.dialog-details {
  margin-bottom: 1.5em;
  .warn {
    color: rgb(247, 81, 81);
    font-size: 1.2em;
  }
  .reply {
    color: rgb(247, 81, 81);
  }
  p {
    margin: 0.5em 0;
    font-size: 1.1em;
  }
}

.edit-title,
.edit-content {
  margin-bottom: 10px;
}

.demonstration {
  margin-right: 2.4vw;
}
.type-radio {
  margin-top: 1.2vw;
  margin-bottom: 1.2vw;
}

@media screen and (max-width: 940px) {
 .note-all{
   .title {
      font-size: 13px;
    }
    .line,
    .info {
      font-size: 13px;
    }
 }
}

@media screen and (max-width: 540px) {
  .note-all{
   .title {
      font-size: 12px;
    }
    .line,
    .info {
      font-size: 12px;
    }
  }
}
</style>
