                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <div class="button-list">
            <el-button class="submitButton" type="primary" @click="onSubmit" v-if="uncreated"></el-button>
            <el-button class="resetButton" @click="resetForm('ruleForm')" v-if="uncreated"></el-button>
            <el-button class="editButton" v-if="!uncreated" @click="submitEditForm()" type="primary"></el-button>
            <el-button class="backButton" @click="toNext()"></el-button>
          </div>
        </el-form>
      </div>
