<script>
import SideLine from "@/views/Practice/SideLine";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { showAsynConfirm } from "@/mixins/messageBox";
import { postTeamInfo, EditTeam } from "@/api/team";
import { getByNetId } from "@/api/common";

export default {
  components: {
    SideLine
  },
  data() {
    var checkNumber = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        if (Number.isInteger(Number(value)) && Number(value) > 0) {
          callback();
        } else {
          callback(new Error(""));
        }
      }, 500);
    };
    var onlyCheckNumber = (rule, value, callback) => {
      if (Number.isInteger(Number(value)) && Number(value) > 0) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    var checkViceName = (rule, value, callback) => {
      if (this.ruleForm.vice_leader_name == null) {
        return callback(new Error(""));
      } else {
        callback();
      }
    };
    var checkTime = (rule, value, callback) => {
      if (isNaN(this._plan_begin_time) || isNaN(this._plan_end_time)) {
        return callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      teams: [],
      practice_types: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: "40"
        },
        {
          value: "7",
          label: ""
        },
        {
          value: "8",
          label: ""
        },
        {
          value: "0",
          label: ""
        }
      ],
      declare_types: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: "40"
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        },
        {
          value: "7",
          label: " "
        },
        {
          value: "8",
          label: ""
        },
        {
          value: "9",
          label: ""
        },
        {
          value: "10",
          label: ""
        },
        {
          value: "11",
          label: ""
        },
        {
          value: "12",
          label: "+"
        },
        {
          value: "0",
          label: ""
        }
      ],
      PRACTICE_TYPE_LIST: [
        "",
        "",
        "",
        "",
        "",
        "",
        "40",
        "",
        ""
      ],
      DECLARATION_TYPE_LIST: [
        "",
        "",
        "",
        "40",
        "",
        "",
        "",
        " ",
        "",
        "",
        "",
        "",
        "+"
      ],
      BELONGTO_LIST: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      ruleForm: {
        team_name: "",
        project_name: "",
        place: "",
        project_type: "",
        plan_region_time: "",
        delivery: false,
        event_content: "",
        event_background: "",
        event_aim: "",
        fee_transport: "",
        fee_print: "",
        fee_other: "",
        //
        vice_leader_net_id: "",
        vice_leader_id: "",
        vice_leader_name: "",
        //
        teacher_name: "",
        belong: "",
        teacher_follow: false,
        is_declared: false,
        declare_type: ""
      },
      rules: {
        team_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        project_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        place: [{ required: true, message: "", trigger: "blur" }],
        plan_region_time: [
          { validator: checkTime, required: true, trigger: "blur" }
        ],
        project_type: [
          { required: true, message: "", trigger: "change" }
        ],
        event_background: [
          { required: true, message: "", trigger: "blur" }
        ],
        event_aim: [
          { required: true, message: "", trigger: "blur" }
        ],
        event_content: [
          { required: true, message: "", trigger: "blur" }
        ],
        fee_transport: [
          { validator: checkNumber, required: true, trigger: "blur" }
        ],
        fee_print: [
          { validator: checkNumber, required: true, trigger: "blur" }
        ],
        fee_other: [
          { validator: onlyCheckNumber, required: false, trigger: "blur" }
        ],
        vice_leader_net_id: [
          { required: true, message: "NetID", trigger: "blur" }
        ],
        vice_leader_name: [
          { validator: checkViceName, required: true, trigger: "blur" }
        ],
        teacher_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        belong: [
          { required: true, message: "", trigger: "change" }
        ],
        is_declared: [{ required: true, message: "", trigger: "change" }],
        teacher_follow: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  computed: {
    ...mapGetters({
      is_declared: "is_declared",
      uncreated: "uncreated",
      active: "active",
      team: "team",
      currentTeamId: "currentTeamId"
    }),
    ...mapMutations({
      setUncreated: 'setUncreated'
    }),
    _plan_begin_time() {
      return parseInt(this.ruleForm.plan_region_time[0] / 1000);
    },
    _plan_end_time() {
      return parseInt(this.ruleForm.plan_region_time[1] / 1000);
    },
    has_created() {
      return false;
    }
  },
  mounted() {
    document.getElementById("container").scrollIntoView({block:"end",behavior:"smooth"});
  },
  created() {
    this.getEditForm();
    const keys = [
      "team_name",
      "project_name",
      "event_background",
      "event_aim",
      "fee_transport",
      "fee_print",
      "place",
      "project_type",
      "plan_region_time",
      "delivery",
      "event_content",
      "fee_other",
      "vice_leader_net_id",
      "vice_leader_id",
      "teacher_name",
      "vice_leader_name",
      "belong",
      "teacher_follow",
      "is_declared",
      "declare_type"
    ];
    for (let i in keys) {
      if (!(typeof(localStorage[keys[i]]) == "undefined") && this.uncreated == true) 
        this.ruleForm[keys[i]] = localStorage.getItem(keys[i]);
    }
  },
  methods: {
    ...mapMutations({
      incrementActive: "incrementActive"
    }),
    ...mapActions({
      changeis_declared: "changeis_declared"
    }),
    // net-id
    async searchViceLeader() {
      try {
        this.$message({
          type: "primary",
          message: "....."
        });
        const data = await getByNetId(this.ruleForm.vice_leader_net_id);
        this.ruleForm.vice_leader_name = data.user_name;
        this.ruleForm.vice_leader_id = data.user_id;
        this.$message({
          type: "success",
          message: ""
        });
      } catch (message) {
        this.$message({
          type: "warning",
          message: message
        });
      }
    },
    // 
    timestampToTime(timestamp) {
      var date = new Date(timestamp * 1000); //10*1000131000
      const Y = date.getFullYear() + "-";
      const M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      const D = this.change(date.getDate()) + " ";
      const h = this.change(date.getHours()) + ":";
      const m = this.change(date.getMinutes()) + ":";
      const s = this.change(date.getSeconds());
      return Y + M + D + h + m + s;
    },
    change(t) {
      if (t < 10) {
        return "0" + t;
      } else {
        return t;
      }
    },
    // 
    onSubmit() {
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.submitForm();
        } else {
          this.$message({
            type: "warning",
            message: ""
          });
          return false;
        }
      });
    },
    // 
    submitForm() {
      showAsynConfirm("", "", done => {
        this._submitForm(done);
      });
    },
    // 
    async _submitForm(done) {
      done();
      try {
        const data = await postTeamInfo(this.parseForm());
        this.$message({
          type: "success",
          message: ""
        });
        this.has_created = true;
        this.clearStorage();
        this.incrementActive(1);
        this.toNext();
      } catch (message) {
        this.$message({
          type: "warning",
          message: ""
        });
      }
    },
    // 
    parseForm() {
      // 
      const keys = [
        "team_name",
        "project_name",
        "project_type",
        "place",
        "event_background",
        "event_aim",
        "event_content",
        "fee_transport",
        "fee_print",
        "fee_other",
        "belong",
        "teacher_follow",
        "vice_leader_id",
        "vice_leader_name",
        "vice_net_id",
        "teacher_name",
        "practice_type",
        "is_declared",
        "declare_type"
      ];
      let params = {};
      for (let i in keys) {
        params[keys[i]] = this.ruleForm[keys[i]];
      }
      // 
      const begin_time = parseInt(this.ruleForm.plan_region_time[0] / 1000);
      const end_time = parseInt(this.ruleForm.plan_region_time[1] / 1000);
      params.plan_begin_time = this.timestampToTime(begin_time);
      params.plan_end_time = this.timestampToTime(end_time);
      if (isNaN(params.belong)) {
        params.belong = this.team.belong;
      }
      if (isNaN(params.project_type)) {
        params.project_type = this.team.project_type;
      }
      if (params.is_declared) {
        params.declare_type = parseInt(params.declare_type);
        if (isNaN(params.declare_type)) {
          params.declare_type = this.team.declare_type;
        }
      } else {
        params.declare_type = 0;
      }
      params.is_declared = params.is_declared ? 1 : 0;
      params.teacher_follow = params.teacher_follow ? 1 : 0;
      return params;
    },
    parseEditForm() {
      // 
      const keys = [
        "team_name",
        "project_name",
        "project_type",
        "place",
        "event_background",
        "event_aim",
        "event_content",
        "fee_transport",
        "fee_print",
        "fee_other",
        "belong",
        "teacher_follow",
        "vice_leader_id",
        "vice_leader_name",
        "teacher_name",
        "practice_type",
        "is_declared",
        "declare_type"
      ];
      for (let i in keys) {
        this.ruleForm[keys[i]] = this.team[keys[i]];
      }
      // 
      // plan_region_time
      let beginTime = Math.floor(
        new Date(this.team.plan_begin_time).getTime() / 1000
      );
      let endTime = Math.floor(
        new Date(this.team.plan_end_time).getTime() / 1000
      );
      // netId
      this.ruleForm.vice_leader_net_id = this.team.vice_net_id
      // 
      this.ruleForm.declare_type = this.DECLARATION_TYPE_LIST[this.team.declare_type];
      // 
      this.ruleForm.belong = this.BELONGTO_LIST[this.team.belong];
      // 
      this.ruleForm.project_type = this.PRACTICE_TYPE_LIST[this.team.project_type];
    },
    getEditForm() {
      if (!this.uncreated) {
        this.parseEditForm();
      }
    },
    submitEditForm() {
      showAsynConfirm("", "", done => {
        this._submitEditForm(done);
      });
    },
    async _submitEditForm(done) {
      done();
      try {
        const data = await EditTeam(this.currentTeamId, this.parseForm());
        this.$message({
          type: "success",
          message: ""
        });

        this.toNext();
      } catch (message) {
        this.$message({
          type: "warning",
          message: ""
        });
      }
    },
    toNext() {
      this.$router.push("/practice/teaminf");
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.clearStorage();
      document.getElementById("container").scrollIntoView({block:"end",behavior:"smooth"});
    },
    clearStorage() {
      const keys = [
        "team_name",
        "project_name",
        "event_background",
        "event_aim",
        "fee_transport",
        "fee_print",
        "place",
        "project_type",
        "plan_region_time",
        "delivery",
        "event_content",
        "fee_other",
        "vice_leader_net_id",
        "vice_leader_id",
        "teacher_name",
        "vice_leader_name",
        "belong",
        "teacher_follow",
        "is_declared",
        "declare_type",
      ];
      for (let i in keys) {
        localStorage.setItem(keys[i], "");
        this.ruleForm[keys[i]] = '';
      }
    }
  },
  beforeDestroy: function() {
    if (this.has_created == false) {
      const keys = [
        "team_name",
        "project_name",
        "event_background",
        "event_aim",
        "fee_transport",
        "fee_print",
        "place",
        "project_type",
        "plan_region_time",
        "delivery",
        "event_content",
        "fee_other",
        "vice_leader_net_id",
        "teacher_name",
        "vice_leader_id",
        "vice_leader_name",
        "belong",
        "teacher_follow",
        "is_declared",
        "declare_type"
      ];
      if(this.uncreated == false) {
        localStorage.setItem('uncreated', this.uncreated);
      }
      for (let i in keys) {
        localStorage.setItem(keys[i], this.ruleForm[keys[i]]);
      }
    }
  }
};
</script>

