<template>
  <div class="panel-notification panel-dev">
    <div class="board">
      <p class="title"></p>
      <pager :pageSize="8" :requestFunc="getList" :errorMsg="''" ref="refresh">
        <el-table
          v-loading="loading"
          slot-scope="props"
          :data="props.list"
          class="notification-board"
          style="width: 100%"
        >
          <el-table-column prop="author_id" label="" width="100"></el-table-column>
          <el-table-column prop="title" label="">
            <template slot-scope="scope">
              <el-tag type="primary" v-if="scope.row.type==1"></el-tag>
              <el-tag type="success" v-if="scope.row.type == 2"></el-tag>
              <el-tag type="info" v-if="scope.row.type == 3"></el-tag>
              <el-tag type="danger" v-if="scope.row.type == 4"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="overtime" label="" width="250"></el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </pager>
    </div>
    <el-dialog
      title=""
      :visible.sync="edit.visible"
      v-dialogDrag
      :fullscreen="isPhone"
      class="notification-edit-dialog"
    >
      <div class="notification-edit-content">
        <el-input class="edit-title" v-model="edit.row.title" placeholder=""></el-input>
        <div class="container">
          <el-input
            type="textarea"
            :rows="4"
            :cols="10"
            class="editor-content"
            placeholder=""
            v-model="edit.row.content"
            id="info-notification-mce"
          ></el-input>
        </div>
        <div class="block">
          <span class="demonstration"></span>
          <el-date-picker
            v-model="edit.row.overtime"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </div>
        <div class="type-radio">
          <span class="demonstration"></span>
          <el-radio-group v-model="edit.row.type">
            <el-radio-button label="1"></el-radio-button>
            <el-radio-button label="2"></el-radio-button>
            <el-radio-button label="3"></el-radio-button>
            <el-radio-button label="4"></el-radio-button>
          </el-radio-group>
        </div>
        <el-button type="primary" @click="submitEdit()"></el-button>
        <el-button @click="edit.visible = false"></el-button>
      </div>
    </el-dialog>
    <div class="editor">
      <p class="title"></p>
      <el-input v-model="currentNotification.title" placeholder=""></el-input>
      <div class="container">
        <el-input
          type="textarea"
          :rows="4"
          :cols="10"
          class="editor-content"
          placeholder=""
          v-model="currentNotification.content"
          id="info-notification-mce"
        ></el-input>
      </div>
      <!-- <mceeditor/>
      <router-view/> -->
      <div class="block">
        <span class="demonstration"></span>
        <el-date-picker
          v-model="currentNotification.overtime"
          type="datetime"
          placeholder=""
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
      </div>
      <div class="type-radio">
        <span class="demonstration"></span>
        <el-radio-group v-model="currentNotification.type">
          <el-radio-button label="1"></el-radio-button>
          <el-radio-button label="2"></el-radio-button>
          <el-radio-button label="3"></el-radio-button>
          <el-radio-button label="4"></el-radio-button>
        </el-radio-group>
      </div>
      <el-button class="submit-button" type="primary" @click="submit()"></el-button>
    </div>
  </div>
</template>

<script>
import Pager from "@/components/ArtPager";
import { mapGetters } from "vuex";
import { showAsynConfirm } from "@/mixins/messageBox";
import {
  getAllNotification,
  addNotification,
  editNotification,
  deleteNotification
} from "@/api/notification";

export default {
  name: "PanelNotification",
  components: {
    Pager
  },
  data() {
    return {
      content: "",
      loading: false,
      notifications: [],
      currentNotification: {
        id: 0,
        content: "",
        title: "",
        overtime: null,
        type: null
      },
      edit: {
        row: {},
        visible: false
      }
    };
  },
  computed: {
    ...mapGetters({
      isPhone: "isPhone"
    })
  },
  methods: {
    //
    getList: getAllNotification,
    async _getAllNotification() {
      this.loading = true;
      try {
        const data = await getAllNotification();
        this.notifications = data.data;
        this.loading = false;
      } catch (message) {
        this.$message.warning(message);
      }
    },
    submit() {
      if (
        this.currentNotification.overtime == null ||
        this.currentNotification.type == null ||
        this.currentNotification.content == "" ||
        this.currentNotification.title == ""
      ) {
        this.$message({
          type: "warning",
          message: ""
        });
      } else {
        showAsynConfirm("", "", done => {
          this._submit(done);
        });
      }
    },
    async _submit(done) {
      try {
        const data = await addNotification(this.currentNotification);
        this.$message({
          type: "success",
          message: ""
        });
      } catch (message) {
        var error_message = "";
        if (message.indexOf("content") != -1) {
          this.error_message = "";
        } else if (message.indexOf("title") != -1) {
          this.error_message = "";
        } else {
          this.error_message = message;
        }
        this.$message({
          type: "warning",
          message: "" + this.error_message
        });
      }
      this.changePages(1);
      done();
      this.currentNotification.content = "";
      this.currentNotification.title = "";
      this.currentNotification.overtime = "";
      this.currentNotification.type = "";
    },
    parseForm(row) {
      // 
      const keys = [
        "title",
        "content",
        "overtime",
        "time",
        "type",
        "notification_id"
      ];
      let params = {};
      for (let i in keys) {
        params[keys[i]] = row[keys[i]];
      }
      return params;
    },
    handleEdit(data) {
      (this.edit.visible = true), (this.edit.row = this.parseForm(data));
      this.temp = this.parseForm(data);
      console.log(this.edit.row);
    },
    submitEdit() {
      if (
        this.edit.row.title == this.temp.title &&
        this.edit.row.content == this.temp.content &&
        this.edit.row.type == this.temp.type &&
        this.edit.row.overtime == this.temp.overtime
      ) {
        this.$message({
          type: "warning",
          message: ""
        });
      } else if (
        this.edit.row.title == "" ||
        this.edit.row.content == "" ||
        this.edit.row.type == "" ||
        this.edit.row.overtime == ""
      ) {
        this.$message({
          type: "warning",
          message: ""
        });
      } else {
        showAsynConfirm("", "", done => {
          this._submitEdit(done);
        });
      }
    },
    async _submitEdit(done) {
      try {
        const data = await editNotification(
          this.edit.row.notification_id,
          this.edit.row
        );
        this.$message({
          type: "success",
          message: ""
        });
      } catch (message) {
        this.$message({
          type: "warning",
          message: message
        });
      }
      this.changePages(1);
      done();
      this.edit.visible = false;
    },
    // 
    handleDelete(row) {
      showAsynConfirm(
        "",
        "",
        done => {
          this._handleDelete(row.notification_id, done);
        }
      );
    },
    //async
    async _handleDelete(id, done) {
      const delete_id = id.toString();
      try {
        const data = await deleteNotification(id);
        this.$message({
          type: "success",
          message: ""
        });
      } catch (message) {
        this.$message({
          type: "warning",
          message: message
        });
      }
      this.changePages(1);
      done();
    },
    onEditorUploadComplete(res) {
      if (res.code == 0) {
        this.$message({
          type: "success",
          message: ""
        });
      } else {
        this.$message({
          type: "error",
          message: res.msg
        });
      }
    },
    set() {
      this.$refs.richText.setContent("");
    },
    get() {
      console.log(this.$refs.richText.getContent());
    },
    changePages(i) {
      this.$refs.refresh.turn(i);
    }
  }
};
</script>

