<template>
  <div class="teamInf" id="teamInf">
    <side-line></side-line>
    <div class="container" id="container">
      <div class="Teams">
        <h2 class="title-h2"></h2>
        <pager :pageSize="6" :requestFunc="getList" :errorMsg="''" ref="refresh">
          <el-table class="team-table" slot-scope="props" :data="props.list" v-loading="loading">
            <el-table-column label="" width="150">
              <template slot-scope="scope">
                <div class="line">{{scope.row.team_name}}</div>
              </template>
            </el-table-column>
            <el-table-column label="" width="150">
              <template slot-scope="scope">
                <div class="line">{{scope.row.project_name}}</div>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="200">
              <template slot-scope="scope">
                <div class="info">
                  <b></b>
                  {{scope.row.leader_name}}
                </div>
                <div class="info">
                  <b></b>
                  {{scope.row.teacher_name}}
                </div>
              </template>
            </el-table-column>
            <el-table-column label="" width="130">
              <template slot-scope="scope">
                <el-popover placement="bottom" width="200" trigger="click">
                  <div class="info">
                    <b></b>
                    {{ scope.row.status | statusToReadable }}
                  </div>
                  <el-button
                    circle
                    slot="reference"
                    size="small"
                    type="info"
                    icon="el-icon-tickets"
                  ></el-button>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column width="150">
              <template slot-scope="scope">
                <el-button class="team-detalis-button" @click="checkOneTeam(scope.row.team_id), openDialog(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </pager>
      </div>
      <div class="teaminf" v-if="isGetTeamId" v-loading="teaminf_loading" id="teamInfoDisplay">
        <TeamInfoDisplay :team-info="dialogRow" :teammates-info="teammates"/>
        <div class="tianchong"></div>
        <div class="actions" v-loading="button_loading">
          <div class="action1">
            <!--  -->
            <div class="row action-button-row" v-if="canEdit">
              <el-button class="actions-button" plain @click="EditTeamInfo()"></el-button>
            </div>
            <!--  -->
            <div class="row action-button-row" v-if="canEdit">
              <el-button
                class="actions-button"
                type="primary"
                plain
                @click="openChangeTeacher(form.teacher_name)"
              ></el-button>
            </div>
          </div>
          <div class="action2">
            <!--  -->
            <div class="row action-button-row" v-if="canSubmit">
              <el-button class="actions-button" type="warning" plain @click="beforeSubmit()"></el-button>
            </div>
            <!--  -->
            <div class="row action-button-row" v-if="canWithDraw">
              <el-button class="actions-button" type="primary" plainplain @click="withDraw()"></el-button>
            </div>
            <!--  -->
            <div class="row action-button-row" v-if="canSetout">
              <el-button class="actions-button" type="warning" plain @click="setOut()"></el-button>
            </div>
            <!--  -->
            <div class="row action-button-row" v-if="canReturn">
              <el-button class="actions-button" type="warning" plain @click="Return()"></el-button>
            </div>
             <!--  -->
            <div class="row action-button-row">
              <el-button class="actions-button0" type="primary" plain @click="Download()">Excel</el-button>
            </div>
          </div>
          <div class="action3">
            <!--  -->
            <div class="row action-button-row">
              <el-button
                class="actions-button"
                type="danger"
                plain
                :disabled="noExit"
                @click="Quit()"
              ></el-button>
            </div>
            <!--  -->
            <div class="row action-button-row" v-if="canDismiss">
              <el-button class="actions-button" type="danger" plain @click="Dismiss()"></el-button>
            </div>
          </div>
          <p class="warnings" style="color:red"></p>
        </div>
      </div>
      <el-dialog
        title=""
        v-dialogDrag
        :fullscreen="isPhone"
        :visible.sync="changeTeacher"
      >
        <el-form :model="form">
          <el-form-item label="  " :label-width="formLabelWidth">
            <el-input class="change-teacher-input" autocomplete="off" v-model="editTeacherName"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button class="actions-button" @click="changeTeacher = false"> </el-button>
          <el-button
            class="actions-button"
            type="primary"
            @click="changeTeacher = false, changeTeacherInfo()"
          > </el-button>
        </div>
      </el-dialog>
      <el-dialog
        title=""
        v-dialogDrag
        :fullscreen="isPhone"
        :visible.sync="problemVisible"
      >
        <p></p>
        <div class="practice-problems">
          <p class="problem" v-for="(p, index) in problems" :key="index">{{ p }}</p>
        </div>
        <p>50~100150~100</p>
        <p></p>
        <el-button type="primary" @click="Submit"></el-button>
        <el-button type="primary" @click="problemVisible = false"></el-button>
      </el-dialog>
      <div class="tianchong" id="TeamInfoDisplay"></div>
      <div class="join">
        <el-button type="primary" @click="toJoinTeams()">&</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import SideLine from "@/views/Practice/SideLine";
import statusMixin from "@/mixins/status";
import { mapGetters, mapMutations } from "vuex";
import {
  getAllTeamInfo,
  changeTeacherName,
  withDraw,
  deleteTeamInfo,
  getTeammateInfo,
  dismissTeam
} from "@/api/team";
import { showAsynConfirm } from "@/mixins/messageBox";
import { openDownloadDialog } from "@/js/download";
import Pager from "@/components/ArtPager";
import TeamInfoDisplay from "@/components/TeamInfoDisplay";

export default {
  name: "TeamInf",
  mixins: [statusMixin],
  components: {
    SideLine,
    Pager,
    TeamInfoDisplay
  },
  data() {
    return {
      dialogVisible: false,
      dialogRow: {},
      // 
      problemVisible: false,
      problems: [],
      // 
      changeTeacher: false,
      editTeacherName: "",
      // 
      teams: [],
      // 
      teammates: [],
      // loading
      loading: false,
      button_loading: false,
      isGetTeamId: false,
      teaminf_loading: false,
      form: {
        team_name: null,
        project_name: null,
        team_id: null,
        code: null,
        place: null,
        plan_begin_time: null,
        plan_end_time: null,
        project_type: null,
        event_background: null,
        event_aim: null,
        event_content: null,
        fee_transport: null,
        fee_print: null,
        fee_other: null,
        belong: 1,
        teacher_name: null,
        teacher_follow: null,
        is_declared: null,
        declare_type: 1,
        status: 1
      },
      formLabelWidth: "150px"
    };
  },
  computed: {
    ...mapGetters({
      cas_user: "cas_user",
      team: "team",
      currentTeamId: "currentTeamId",
      user_info: "user_info",
      isPhone: "isPhone"
    }),
    isLeader() {
      return this.user_info.user_id == this.form.leader_id;
    },
    isViceLeader() {
      return this.user_info.user_id == this.form.leader_id;
    },
    canEdit() {
      return this.form.status == 0 || (this.form.status == 2 && this.isLeader);
    },
    canSubmit() {
      return this.form.status == 0 || (this.form.status == 2 && this.isLeader);
    },
    canWithDraw() {
      return this.form.status == 1 && this.isLeader;
    },
    canSetout() {
      return this.form.status == 3 && this.isLeader;
    },
    canReturn() {
      return this.form.status == 4 && this.isLeader;
    },
    noExit() {
      return this.isLeader || this.isViceLeader;
    },
    canDismiss() {
      return this.isLeader;
    }
  },
  mounted() {
    document.getElementById("container").scrollIntoView({block:"end",behavior:"smooth"});
  },
  methods: {
    getList: getAllTeamInfo,
    ...mapMutations({
      setTeam: "setTeam",
      saveCurrentTeamId: "saveCurrentTeamId",
      setUncreated: "setUncreated",
      setSecondLoad: "setSecondLoad"
    }),
    async getTeammateInfo() {
      this.loading = true;
      try {
        const data = await getTeammateInfo(this.currentTeamId);
        this.teammates = data.data;
        this.loading = false;
      } catch (message) {
        this.$message.warning(message);
      }
    },
    checkOneTeam(data) {
      this.saveCurrentTeamId(data);
      this.teaminf_loading = true;
      setTimeout(() => {
        this.getTeamInf(), (this.isGetTeamId = true);
        this.getTeammateInfo();
        this.dialogVisible = true;
      }, 100);
      setTimeout(() => {
        this.teaminf_loading = false;
      }, 600);
    },
    openDialog(row) {
      this.dialogRow = row;
      this.button_loading = true;
      setTimeout(() => {
        this.button_loading = false;
        this.rollDown()
      }, 1000);
    },
    rollDown() {
      document.getElementById("TeamInfoDisplay").scrollIntoView({block:"end",behavior:"smooth"});
    },
    toJoinTeams() {
      this.setUncreated(true);
      this.$router.push("/practice");
      this.setSecondLoad(0);
    },
    // 
    getTeamInf() {
      axios
        .get("/api/team/" + this.currentTeamId)
        .then(response => {
          const data = response.data.data;
          this.setTeam(data);
          this.dialogRow = data;
          this.form = data;
        })
        .catch(err => {
          this.$message({
            type: "warning",
            message: err.err_msg
          });
        });
    },
    // 
    EditTeamInfo() {
      this.setUncreated(false);
      document.getElementById("container").scrollIntoView({block:"end",behavior:"smooth"});
      this.$router.push("/practice/getinfo");
    },
    // 
    openChangeTeacher(data) {
      this.changeTeacher = true;
      this.editTeacherName = data
    },
    // 
    changeTeacherInfo() {
      showAsynConfirm(
        `${this.editTeacherName}`,
        "",
        done => {
          this._changeTeacherInfo(done);
        }
      );
    },
    async _changeTeacherInfo(done) {
      done();
      this.changePages(1);
      try {
        const data = await changeTeacherName(this.currentTeamId, this.editTeacherName);
        this.checkOneTeam(this.currentTeamId)
        this.$message({
          type: "success",
          message: ""
        });
      } catch (message) {
        this.$message.warning(message);
      }
    },
    Download() {
      openDownloadDialog("/api/outExcel/" + this.currentTeamId.toString());
      this.$message("");
    },
    beforeSubmit() {
      let project = this.team;
      let backgroundLen = project.event_background.length;
      let aimLen = project.event_aim.length;
      let contentLen = project.event_content.length;
      let totalLen = backgroundLen + aimLen + contentLen;
      function tryAddTip(tips, name, len) {
        if (len < 50 || len > 100) {
          let tip = `${name}${len}${len < 50 ? "" : ""}`;
          tips.push(tip);
        }
      }
      let tips = [];
      tryAddTip(tips, "", backgroundLen);
      tryAddTip(tips, "", aimLen);
      tryAddTip(tips, "", contentLen);
      if (totalLen < 150 || totalLen > 300) {
        let tip = `${totalLen}${
          totalLen < 150 ? "" : ""
        }`;
        tips.push(tip);
      }
      if (tips.length > 0) {
        this.problems = tips;
        this.problemVisible = true;
      } else {
        this.Submit();
      }
    },
    Submit() {
      showAsynConfirm("", "", done => {
        this._Submit(done);
      });
    },
    _Submit(done) {
      axios
        .put("/api/submitTeam/" + this.currentTeamId)
        .then(response => {
          done();
          this.changePages(1);
          if (response.data.success == 1) {
            this.$message({
              type: "success",
              message: ""
            });
            this.checkOneTeam(this.currentTeamId)
            this.button_loading = true;
            this.problemVisible = false;
            setTimeout(() => {
              this.button_loading = false;
            }, 500);
          } else {
            this.$message({
              type: "warning",
              message: ""
            });
          }
        })
        .catch(err => {
          this.$message({
            type: "warning",
            message: err.err_msg
          });
        });
    },
    withDraw() {
      showAsynConfirm("", "", done => {
        this._withDraw(done);
      });
    },
    async _withDraw(done) {
      try {
        const data = await withDraw(this.currentTeamId);
        this.$message({
          type: "success",
          message: ""
        });
        this.checkOneTeam(this.currentTeamId)
        this.button_loading = true;
        setTimeout(() => {
          this.button_loading = false;
        }, 500);
      } catch (message) {
        this.$message({
          type: "error",
          message: "" + message
        });
      }
      done();
      this.changePages(1);
    },
    setOut() {
      showAsynConfirm(
        "",
        "",
        done => {
          this._setOut(done);
        }
      );
    },
    _setOut(done) {
      axios
        .put("/api/outTeam/" + this.currentTeamId)
        .then(response => {
          done();
          this.changePages(1);
          if (response.data.success == 1) {
            this.$message({
              type: "success",
              message: ""
            });
            this.getTeams();
            this.getTeamInf();
            this.button_loading = true;
            setTimeout(() => {
              this.button_loading = false;
            }, 500);
          } else {
            this.$message({
              type: "warning",
              message: ""
            });
          }
        })
        .catch(err => {
          this.$message({
            type: "warning",
            message: err.err_msg
          });
        });
    },
    Return() {
      showAsynConfirm(
        "",
        "",
        done => {
          this._Return(done);
        }
      );
    },
    _Return(done) {
      axios
        .put("/api/returnTeam/" + this.currentTeamId)
        .then(response => {
          done();
          if (response.data.success == 1) {
            this.$message({
              type: "success",
              message: ""
            });
            this.getTeams();
            this.getTeamInf();
            this.changePages(1);
            this.button_loading = true;
            setTimeout(() => {
              this.button_loading = false;
            }, 500);
          } else {
            this.$message({
              type: "warning",
              message: ""
            });
          }
        })
        .catch(err => {
          this.$message({
            type: "warning",
            message: err.err_msg
          });
        });
    },
    Quit() {
      showAsynConfirm("", "", done => {
        this._Quit(done);
      });
    },
    async _Quit(done) {
      try {
        const data = await deleteTeamInfo(this.currentTeamId);
        this.$message({
          type: "success",
          message: ""
        });
        this.changePages(1);
        this.$router.push("/practice/teaminf");
      } catch (message) {
        this.$message({
          type: "error",
          message: "" + message
        });
      }
      done();
    },
    Dismiss() {
      showAsynConfirm(
        "",
        "",
        done => {
          this._Dismiss(done);
        }
      );
    },
    async _Dismiss(done) {
      try {
        const data = await dismissTeam(this.currentTeamId);
        this.$message({
          type: "success",
          message: ""
        });
        this.changePages(1);
        this.$router.push("/practice");
      } catch (message) {
        this.$message({
          type: "error",
          message: "" + message
        });
      }
      done();
    },
    changePages(i) {
      this.$refs.refresh.turn(i);
    }
  }
};
</script>


